import React, { Fragment, useCallback, useState } from 'react';
import Modal, {
  ModalBody,
  ModalFooter,
  ModalHeader,
  ModalTitle,
  ModalTransition,
} from '@atlaskit/modal-dialog';

export default function VideoSetting({onChange,state}) {
  const [isOpen, setIsOpen] = useState(false);

  const openModal = useCallback(() => setIsOpen(true), []);
  const closeModal = useCallback(() => setIsOpen(false), []);

  const onClick = useCallback((e) => {
    //closeModal();
  },[]);

 return (
    <>
      <button onClick={openModal}>Video Setting</button>

     <ModalTransition>
        {isOpen && (
          <Modal onClose={closeModal}>
          <ModalHeader>Video Setting</ModalHeader>
         <ModalBody>
         </ModalBody>
         <ModalFooter>
             <button onClick={closeModal}>Close</button>
         </ModalFooter>
         </Modal>
        )}
      </ModalTransition>
    </>
  );
}
