import React, { Fragment, useCallback, useState } from 'react';
import Modal, {
  ModalBody,
  ModalFooter,
  ModalHeader,
  ModalTitle,
  ModalTransition,
} from '@atlaskit/modal-dialog';

export default function RoomSelectionModal({onSelected,rooms}) {
  const [isOpen, setIsOpen] = useState(false);

  const openModal = useCallback(() => setIsOpen(true), []);
  const closeModal = useCallback(() => setIsOpen(false), []);

  const options = rooms.map( (option) =>
   <option key={option} value={option}>{option}</option>
  );

  const onChange = useCallback((e) => {
    onSelected(e.target.value); // parent
    // console.log(e.target.value);
    closeModal();
  },[]);

 return (
    <>
      <button onClick={openModal}>Select a room</button>

     <ModalTransition>
        {isOpen && (
          <Modal onClose={closeModal}>
          <ModalHeader>Select a room to join</ModalHeader>
         <ModalBody>
          <select onChange={onChange}>
          {options}
          </select>
          </ModalBody>
          <ModalFooter>
             <button onClick={closeModal}>Close</button>
          </ModalFooter>
          </Modal>
        )}
      </ModalTransition>
    </>
  );
}
