import React, { useState, useRef, useCallback }  from 'react';
import './App.css';
import LoginModal from './lib/LoginModal.js';
import * as utils from  './lib/MediaDevices.js';
import RoomSelectionModal from './lib/RoomSelectionModal.js';
import AudioSettingModal from './lib/AudioSettingModal.js';
import VideoSettingModal from './lib/VideoSettingModal.js';

function App() {
  const localVideo = useRef();
  const remoteVideo = useRef();
  const [isInitialized,setIsInitialized] = useState(false);
  const [state,setState] = useState({});

  const [audioSettings,setAudioSettings] = useState({
    autoGainControl: false, 
    echoCancellation: false, 
    noiseSuppression: false 
  });
  const [videoSettings,setVideoSettings] = useState({
    width: {ideal: 1920}, height: {ideal: 1080}
  });

  const constructor = async () => {
    if(isInitialized) return;
    console.log('CONSTRUCTOR');
    setIsInitialized(true);
 
    let stream = null;  
    try {
      stream = await navigator.mediaDevices.getUserMedia({
        video: videoSettings,
        audio: audioSettings
      });
    } catch(err) {console.error(err);}

    console.log(stream.getTracks()); // Always twice with npm start 
    try {
      localVideo.current.srcObject = stream;

      const initialState = {
        localStream: stream,
        remoteStream: null
      }
      setState(initialState);
    } catch(err) {console.error(err);}

  }


  const loginHandler = useCallback((data) => {
    console.log(data);
  },[]);

  const roomHandler = useCallback((data) => {
    console.log(data);
  },[]);

  const audioSettingHandler = useCallback((data) => {
    console.log(data);
  },[]);

  const videoSettingHandler = useCallback((data) => {
    console.log(data);
  },[]);


  const rooms = ['room0', 'room1', 'room2','room3']; 

  constructor(); // start program

  return (
    <div className="App">
    <LoginModal onLogin={loginHandler} /> &emsp;
    <RoomSelectionModal onSelected={roomHandler} rooms={rooms}/> &emsp;
    <AudioSettingModal onSelected={audioSettingHandler} state={audioSettings}/>
    &emsp;
    <VideoSettingModal onSelected={videoSettingHandler} state={videoSettings}/>
    <hr />
    <video ref={localVideo} width="360" controls autoPlay playsInline muted />
    <video ref={remoteVideo} width="640" controls autoPlay playsInline
     style={{width: '100%'}} />
    <hr />
    </div>
  );
}

export default App;
